import { Expense } from "./ExpenseForm";
import { useState, useEffect } from "react"; // Don't forget to import useEffect
import Layout from "../components";

export default function ExpensesPage() {
  // Initialize expenses as empty array instead of undefined
  const [expenses, setExpenses] = useState<Expense[]>([]); 
  const [searchTerm, setSearchTerm] = useState("");
  const [sortConfig, setSortConfig] = useState<{ 
    key: keyof Expense; 
    direction: 'asc' | 'desc' 
  } | null>(null);
  const [isLoading, setIsLoading] = useState(true); // Add loading state
  const [error, setError] = useState<string | null>(null); // Add error state

  // Fetch expenses with error handling
  useEffect(() => {
    const fetchExpenses = async () => {
      try {
        setIsLoading(true);
        const response = await fetch("/api/expenses");
        if (!response.ok) {
          throw new Error('Failed to fetch expenses');
        }
        const data = await response.json();
        setExpenses(data);
      } catch (err) {
        setError(err.message);
      } finally {
        setIsLoading(false);
      }
    };

    fetchExpenses();
  }, []);

  // Filter expenses - now safe because expenses is always an array
  const filteredExpenses = expenses.filter(expense =>
    expense.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    expense.category.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // Sort expenses - also safe
  const sortedExpenses = [...filteredExpenses].sort((a, b) => {
    if (!sortConfig) return 0;
    if (a[sortConfig.key] < b[sortConfig.key]) {
      return sortConfig.direction === 'asc' ? -1 : 1;
    }
    if (a[sortConfig.key] > b[sortConfig.key]) {
      return sortConfig.direction === 'asc' ? 1 : -1;
    }
    return 0;
  });

  const requestSort = (key: keyof Expense) => {
    let direction: 'asc' | 'desc' = 'asc';
    if (sortConfig && sortConfig.key === key && sortConfig.direction === 'asc') {
      direction = 'desc';
    }
    setSortConfig({ key, direction });
  };

  if (isLoading) {
    return (
      <Layout>
        <div style={{ padding: "2rem", textAlign: "center" }}>
          <p>Loading expenses...</p>
        </div>
      </Layout>
    );
  }

  if (error) {
    return (
      <Layout>
        <div style={{ padding: "2rem", textAlign: "center", color: "#dc3545" }}>
          <p>Error: {error}</p>
        </div>
      </Layout>
    );
  }

  return (
    <Layout>
      <div style={{
        padding: "2rem",
        maxWidth: "1200px",
        margin: "0 auto",
        minHeight: "100vh"
      }}>
        {/* Page Header */}
        <div style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          marginBottom: "2rem",
          flexWrap: "wrap",
          gap: "1rem"
        }}>
          <h1 style={{
            margin: 0,
            color: "#6a0dad",
            fontSize: "1.8rem",
            fontWeight: "600"
          }}>All Expenses</h1>
          
          <div style={{ position: "relative" }}>
            <input
              type="text"
              placeholder="Search expenses..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              style={{
                padding: "0.5rem 1rem",
                border: "1px solid #ddd",
                borderRadius: "4px",
                minWidth: "250px"
              }}
            />
          </div>
        </div>

        {/* Expenses Table */}
        <div style={{
          backgroundColor: "white",
          borderRadius: "8px",
          boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
          overflow: "hidden"
        }}>
          {/* Table Header */}
          <div style={{
            display: "grid",
            gridTemplateColumns: "2fr 1fr 1fr 1fr",
            padding: "1rem",
            backgroundColor: "#f8f9fa",
            fontWeight: "600",
            borderBottom: "1px solid #eee"
          }}>
            <div onClick={() => requestSort('title')} style={{ cursor: "pointer" }}>
              Title {sortConfig?.key === 'title' && (sortConfig.direction === 'asc' ? '↑' : '↓')}
            </div>
            <div onClick={() => requestSort('amount')} style={{ cursor: "pointer", textAlign: "right" }}>
              Amount {sortConfig?.key === 'amount' && (sortConfig.direction === 'asc' ? '↑' : '↓')}
            </div>
            <div onClick={() => requestSort('category')} style={{ cursor: "pointer" }}>
              Category {sortConfig?.key === 'category' && (sortConfig.direction === 'asc' ? '↑' : '↓')}
            </div>
            <div onClick={() => requestSort('date')} style={{ cursor: "pointer" }}>
              Date {sortConfig?.key === 'date' && (sortConfig.direction === 'asc' ? '↑' : '↓')}
            </div>
          </div>

          {/* Table Body */}
          {sortedExpenses.length === 0 ? (
            <div style={{ padding: "2rem", textAlign: "center", color: "#6c757d" }}>
              {searchTerm ? "No matching expenses found" : "No expenses recorded yet"}
            </div>
          ) : (
            sortedExpenses.map((expense, index) => (
              <div 
                key={index}
                style={{
                  display: "grid",
                  gridTemplateColumns: "2fr 1fr 1fr 1fr",
                  padding: "1rem",
                  borderBottom: index === sortedExpenses.length - 1 ? "none" : "1px solid #eee",
                  alignItems: "center",
                  
                }}
              >
                <div style={{ fontWeight: "500" }}>{expense.title}</div>
                <div style={{ textAlign: "right", color: "#dc3545", fontWeight: "500" }}>
                  ₹{Number(expense.amount).toLocaleString('en-IN')}
                </div>
                <div>
                  <span style={{
                    backgroundColor: "#f0f0f0",
                    padding: "0.25rem 0.5rem",
                    borderRadius: "4px",
                    fontSize: "0.85rem"
                  }}>
                    {expense.category}
                  </span>
                </div>
                <div style={{ color: "#6c757d", fontSize: "0.9rem" }}>
                  {new Date(expense.date).toLocaleDateString()}
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    </Layout>
  );
}